import React from 'react';

import Radio from '@material-ui/core/Radio';
import FormControl from '@material-ui/core/FormControl';
import FormLabel from '@material-ui/core/FormLabel';
import FormHelperText from '@material-ui/core/FormHelperText';
import Button from '@material-ui/core/Button';
import FormGroup from '@material-ui/core/FormGroup';

import Table from '@material-ui/core/Table';
import TableBody from '@material-ui/core/TableBody';
import TableCell from '@material-ui/core/TableCell';
import TableContainer from '@material-ui/core/TableContainer';
import TableHead from '@material-ui/core/TableHead';
import TableRow from '@material-ui/core/TableRow';
import Paper from '@material-ui/core/Paper';
import { makeStyles } from '@material-ui/core/styles';
import StatisticsComponent from './StatisticsComponent'
import Grid from '@material-ui/core/Grid';
import { properties } from '../Properties/Properties.js';

const useStyles = makeStyles((theme) => ({
  cell: {
    padding: "3px"
  },
}))

export default function RatingSurveyComponent (props){
    const classes = useStyles()
    const [state, setState] = React.useState({
        helperText: " ,    ",
    })
    const [errors, setErrors] = React.useState({
      option: "",
      duplicate: ""
    })
    // const [selectedValues, setSelectedValues] = React.useState({})
    const [selectedValues, setSelectedValues] = React.useState(new Array(props.Rows.length))

    const [options, setOptions] = React.useState({})
    const [voted, setVoted] = React.useState(props.Voted)
    const [SurveyId, setSurveyId] = React.useState(props.SurveyId)
    const [body, setBody] = React.useState()
    const [img, setImg] = React.useState(props.Image)

    const validate = () =>{
      let local_errors = 0
      let duplicate = false

      if ((new Set(selectedValues.filter(val => val !== undefined))).size !== selectedValues.filter(val => val !== undefined).length){
        duplicate = true
      }
      local_errors = duplicate ? local_errors + 1 : local_errors
      setErrors({
        ...errors, 
        duplicate: duplicate ? '       / ' : '',
        option: (selectedValues.filter(val => val !== undefined).length < props.Rows.length) ? '  ' : ''
      })
      local_errors = selectedValues.filter(val => val !== undefined).length  < props.Rows.length ? local_errors + 1 : local_errors
      return local_errors
    }
    const handleSubmit = (event) => {
      event.preventDefault()
      if (validate() === 0){
        fetch(`${properties.address}/answerSurvey`, 
        {
          method: 'POST',
          headers: {
            "Content-Type": "application/json"
          },
          body: JSON.stringify({
            "surveyId": props.SurveyId,
            "rowsColumns" : selectedValues
          }),
          redirect: 'follow',
          credentials: 'include'
        })
        .then(response => {
            if (response.status === 200){
              props.votedCallback()
              setVoted(true)
            }
            else if (response.status === 404 || response.status === 403 ){
              props.History.push('/login')
            }
          }
        )
      }
    }
    const handleChange = (event, row_index) => {
        // setSelectedValues({...selectedValues, [event.target.name]: event.target.value});
        let old_values = selectedValues
        if (!old_values.includes(event.target.value)){
          old_values[row_index] = event.target.value
          setSelectedValues(old_values);
          setBody(returnBody())
        }
        else{
          let i = old_values.indexOf(event.target.value)
          old_values[i] = undefined
          old_values[row_index] = event.target.value
          setSelectedValues(old_values);
          setBody(returnBody())
        }
    }
    const returnBody = e => {
      let body
      if (voted && !props.flipped){
        body = <StatisticsComponent Rows = {props.Rows} Columns = {props.Columns} Type = "Rating" SurveyId = {SurveyId}></StatisticsComponent>
      }
      else{
        body = <Grid container> 
            <Grid item xs = {img === '' || img === null ? 0 : 4}>
              <img alt = "" style = {{width: "100%", maxWidth: "200px"}} src = {img}></img>
            </Grid>
            <Grid item xs = {img === "" || img === null ? 12 : 8}>
            <form onSubmit={handleSubmit}>
              <FormControl 
                component="fieldset"
                error = {Object.values(errors).filter(value => value !== '').length > 0}
              >
                <FormLabel component="legend">{props.Question}</FormLabel>
                <FormGroup>
                  <TableContainer component={Paper}>
                        <Table aria-label="simple table">
                          <TableHead>
                            <TableRow>
                              <TableCell className = {classes.cell}></TableCell>
                              {props.Columns.map((column, column_index) => (
                                <TableCell key = {column_index} className = {classes.cell} align="center">{column}</TableCell>
                              ))}
                            </TableRow>
                          </TableHead>
                          <TableBody>
                            {props.Rows.map((row, row_index) => (
                              <TableRow key = {row_index}>
                                <TableCell className = {classes.cell} component="th" scope="row">
                                  {row}
                                </TableCell>
                                {props.Columns.map((column, column_index) => (
                                  
                                  <TableCell key = {column_index} align="center" className = {classes.cell}>
                                    {/* <FormControlLabel control={<Radio name = {"r" + row_index} onChange = {handleChange} />}/> */}
                                    <Radio 
                                      name = {"r" + row_index} 
                                      onChange = {e => {handleChange(e, column_index)}} 
                                      // checked= {selectedValues["r" + row_index] === "c" + column_index}
                                      checked= {selectedValues[column_index] === props.Rows[row_index]}
                                      // value = {"c" + column_index}
                                      value = {props.Rows[row_index]}
                                    />
                                  </TableCell>
                                ))}
                            </TableRow>
                          ))}
                      </TableBody>
                    </Table>
                  </TableContainer>
                </FormGroup>
                <FormHelperText>{state.helperText}</FormHelperText>
                {Object.keys(errors).map(key => 
                  <FormHelperText>{errors[key]}</FormHelperText>
                )}
                
                <Button type = "submit" variant="contained" color="primary" onChange = {handleSubmit}></Button>
              </FormControl>
            </form>
          </Grid>
        </Grid>
      }

      return body
    }
    // React.useEffect(() => {
    //   setBody(returnBody())
    // },[])
    // setBody(returnBody())
      return <>
        {returnBody()}
        {/* {body} */}
      </>
      
      
    }