import React from 'react';

import Radio from '@material-ui/core/Radio';
import RadioGroup from '@material-ui/core/RadioGroup';
import FormControlLabel from '@material-ui/core/FormControlLabel';
import FormControl from '@material-ui/core/FormControl';
import FormLabel from '@material-ui/core/FormLabel';
import FormHelperText from '@material-ui/core/FormHelperText';
import Button from '@material-ui/core/Button';
import Grid from '@material-ui/core/Grid';
import StatisticsComponent from './StatisticsComponent'

import Dialog from '@material-ui/core/Dialog';
import DialogActions from '@material-ui/core/DialogActions';
import DialogContent from '@material-ui/core/DialogContent';
import DialogContentText from '@material-ui/core/DialogContentText';
import DialogTitle from '@material-ui/core/DialogTitle';
import CircularProgress from '@material-ui/core/CircularProgress';
import CropOriginalIcon from '@material-ui/icons/CropOriginal';
import { properties } from '../Properties/Properties.js';

export default class SingleSurveyComponent extends React.Component{
    constructor(props){
      super(props)
      
      this.state = {
        choosedOption: "",
        value: "",
        helperText: " ,    ",
        error: false,
        img: props.Image, // doesn't work this way
        // img: "",
        voted: props.Voted,
        flipped: props.flipped,
        open: false
      }
      this.SurveyId = props.SurveyId
      this.History = props.History
      this.votedCallback = props.votedCallback
    }
    handleSubmit = (event) => {
      event.preventDefault();
      if (this.state.value === '') {
        this.setHelperText(' ');
        this.setState({error: true})
      }
      else{
        this.setHelperText(' ,    ');
        this.setState({error: false})
      }
      fetch(`${properties.address}/answerSurvey`, 
      {
        method: 'POST',
        headers: {
          "Content-Type": "application/json"
        },
        body: JSON.stringify({
          "surveyId": this.SurveyId,
          "choosedOption" : this.state.value
        }),
        redirect: 'follow',
        credentials: 'include'
      })
      .then(response => {
          if (response.status === 200){
            this.votedCallback()
            this.setState({...this.state, voted : true})
          }
          else if (response.status === 404 || response.status === 403 ){
            this.History.push('/login')
          }
        }
      )
    }
    setHelperText = (text) => {
      this.setState({helperText: text})
    }
    handleChange = (event) => {
      this.setState({value: event.target.value})
    }
    optionChanged = (option) =>{
      this.setState({choosedOption: option})
    }
    handleClose = () => {
      this.setState({open: false})
    }
    handleOpen = surveyToDelete => {
      this.setState({open: true})
    }
    returnBody = e => {
      let body
      if (this.state.voted && !this.props.flipped){
        body = <StatisticsComponent Options = {this.props.Options} Type = "Single" SurveyId = {this.SurveyId}></StatisticsComponent>
      }
      else{
        body = <><form onSubmit={this.handleSubmit}>
        <Grid container>
          <Grid item xs = {this.props.Image === '' || this.props.Image === null ? 0 : 4}>
            <img onClick = {this.handleOpen} alt = "" style = {{width: "100%", maxWidth: "200px", cursor: "pointer"}} src = {this.props.Image}></img>
            </Grid>
            <Grid item xs = {this.props.Image === "" || this.props.Image === null ? 12 : 8}>
              <FormControl 
                component="fieldset"
                error = {this.state.error}
              >
                  <FormLabel component="legend">{this.props.Question}</FormLabel>
                  <RadioGroup aria-label="gender" value={this.state.value} onChange = {this.handleChange}>
                  {this.props.Options.map( (option, index) =>
                      <FormControlLabel value={option} control={<Radio />} label={option} key = {index} />
                  )}
                  </RadioGroup>
                  <FormHelperText>{this.state.helperText}</FormHelperText>
                  <Button type = "submit" variant="contained" color="primary" ></Button>
            </FormControl>
          </Grid>
        </Grid>
      </form>
      <Dialog
        open={this.state.open}
        onClose={this.handleClose}
      >
        <DialogContent>
          <DialogContentText>
            <img alt = "" style = {{width: "100%"}} src = {this.state.img}></img>
          </DialogContentText>
        </DialogContent>
        <DialogActions>
          <Button onClick={this.handleClose} color="primary">
            
          </Button>
        </DialogActions>
      </Dialog>
      </>
      }
      return body
    }
    render(){
      return <>
        {this.returnBody()}
      </>
    }
  }