import React, { Children, cloneElement } from 'react';
import { makeStyles } from '@material-ui/core/styles';
import clsx from 'clsx';
import Card from '@material-ui/core/Card';
import CardHeader from '@material-ui/core/CardHeader';
import CardContent from '@material-ui/core/CardContent';
import CardActions from '@material-ui/core/CardActions';
import Collapse from '@material-ui/core/Collapse';
import Avatar from '@material-ui/core/Avatar';
import IconButton from '@material-ui/core/IconButton';
import Typography from '@material-ui/core/Typography';
import { red } from '@material-ui/core/colors';
import FavoriteIcon from '@material-ui/icons/Favorite';
import ExpandMoreIcon from '@material-ui/icons/ExpandMore';
import CheckIcon from '@material-ui/icons/Check';
import Tooltip from '@material-ui/core/Tooltip';
import LoopIcon from '@material-ui/icons/Loop';
import MoreVertIcon from '@material-ui/icons/MoreVert';
import Menu from '@material-ui/core/Menu';
import MenuItem from '@material-ui/core/MenuItem';
import ListItemText from '@material-ui/core/ListItemText';
import ListItemAvatar from '@material-ui/core/ListItemAvatar';
import List from '@material-ui/core/List';
import ListItem from '@material-ui/core/ListItem';
import Divider from '@material-ui/core/Divider';
import ThumbUpAltIcon from '@material-ui/icons/ThumbUpAlt';
import TextField from '@material-ui/core/TextField';
import Button from '@material-ui/core/Button';
import FormControlLabel from '@material-ui/core/FormControlLabel';
import FormControl from '@material-ui/core/FormControl';
import FormLabel from '@material-ui/core/FormLabel';
import FormHelperText from '@material-ui/core/FormHelperText';
import DeleteOutlineOutlinedIcon from '@material-ui/icons/DeleteOutlineOutlined';
import { properties } from '../Properties/Properties.js';
import { Link } from 'react-router-dom';

const useStyles = makeStyles((theme) => ({
  // list: {
  //   width: '100%',
  //   maxWidth: '36ch',
  //   backgroundColor: theme.palette.background.paper,
  // },
  inline: {
    display: 'inline',
  },
  root: {
    // maxWidth: 345,
    overflowX: "auto",
    marginBottom: "10px"
  },
  media: {
    height: 0,
    paddingTop: '56.25%', // 16:9
  },
  expand: {
    transform: 'rotate(0deg)',
    // marginLeft: 'auto',
    transition: theme.transitions.create('transform', {
      duration: theme.transitions.duration.shortest,
    }),
  },
  expandOpen: {
    transform: 'rotate(180deg)',
  },
  avatar: {
    backgroundColor: red[500],
  },
  header: {
    padding: 2
  },
  actions: {
    padding: 2
  },
  content: {
    padding: 8
  },
  tag: {
    margin: 2,
    backgroundColor: "#a2a2dc",
    margin: 2,
    borderRadius: 5,
    cursor: "pointer",
    '&:hover': {
      backgroundColor: "#c3c3ec",
    },
    fontSize: 15,
    color: "#2f4f4f",
    paddingLeft: 5,
    paddingRight: 5
  },
  icon: {
    padding: 3
  },
  action: {
    margin: 0
  }

}));

export default function CardComponent(props) {
  const classes = useStyles();
  const [expanded, setExpanded] = React.useState(false);
  const [likes, setLikes] = React.useState(props.Likes)
  const [voted, setVoted] = React.useState(props.Voted)
  const [answered, setAnswered] = React.useState(props.Answered)
  const [flipped, setFlipped] = React.useState(!false)
  const [anchorEl, setAnchorEl] = React.useState(null);
  const [comment, setComment] = React.useState("");
  const [errors, setErrors] = React.useState({});
  const [state, setState] = React.useState({
    comments: props.Comments
  })
  const { UserId, LoggedInUserId } = props
  const handleExpandClick = () => {
    setExpanded(!expanded);
  };
  const handleLike = e => {
    fetch(`${properties.address}/like`,
      {
        headers: {
          'Content-Type': 'application/json'
        },
        method: 'POST',
        credentials: 'include',
        body: JSON.stringify({ surveyId: props.ElementId })
      })
      .then(response => response.text())
      .then(result => {
        if (result === 'Added') {
          setLikes(likes + 1)
          setVoted(true)
        }
        else if (result === 'Deleted') {
          setLikes(likes - 1)
          setVoted(false)
        }
      })
  }
  const handleFlip = e => {
    setFlipped(!flipped)
  }
  const votedCallback = e => {
    setAnswered(true)
    setFlipped(false)
  }
  const handleClick = (event) => {
    setAnchorEl(event.currentTarget);
  }
  const handleClose = () => {
    setAnchorEl(null);
  }
  const handleDelete = () => {
    fetch(`${properties.address}/deleteSurvey`,
      {
        method: 'DELETE',
        headers: {
          'Content-Type': 'application/json'
        },
        body: JSON.stringify({ surveyId: props.ElementId }),
        redirect: 'follow',
        credentials: 'include',
      })
      .then(response => {
        if (response.status === 200) {
          alert('deleted')
        }
      })
  }
  const validateComment = e => {
    let errs = 0
    if (comment.length < 5 || comment.length > 200) {
      errs++
      setErrors({ ...errors, comment: "  5    200 " })
    }
    else {
      setErrors({ ...errors, comment: "" })
    }
    return errs
  }
  const handleCommentSubmit = e => {
    e.preventDefault()
    if (validateComment() == 0) {
      fetch(`${properties.address}/addComment`,
        {
          headers: {
            'Content-Type': 'application/json'
          },
          method: 'POST',
          credentials: 'include',
          body: JSON.stringify({
            content: comment,
            surveyId: props.ElementId
          })
        })
        .then(response => {
          if (response.status == 200) {
            return response.json()
          }
        })
        .then(result => {
          let new_comments = state.comments

          new_comments.push({
            "content": comment,
            "likes": 0,
            "user_id": result.user_id,
            "username": props.LoggedInUsername,
            "date": result.date,
            "id": result.id,
          })

          setState({ ...state, comments: new_comments })
        })
    }

  }
  const handleCommentLike = comment => {
    fetch(`${properties.address}/commentLike`,
      {
        headers: {
          'Content-Type': 'application/json'
        },
        method: 'POST',
        credentials: 'include',
        body: JSON.stringify({ commentId: comment.id })
      })
      .then(response => {
        if (response.status == 200) {
          return response.json()
        }
      }).then(result => {
        if (result) {
          // new_comments = comments.find(comment -> comment.id === commentId)
          comment.likes = result.likes
          comment.voted = result.voted
          setState({ ...state, comments: state.comments })
        }
      })
  }
  const handleDeleteComment = commentId => {
    fetch(`${properties.address}/deleteComment`,
      {
        method: 'DELETE',
        headers: {
          'Content-Type': 'application/json'
        },
        body: JSON.stringify({
          commentId: commentId,
          surveyId: props.ElementId
        }),
        redirect: 'follow',
        credentials: 'include',
      })
      .then(response => {
        if (response.status === 200) {
          let new_comments = state.comments.filter(comment => comment.id !== commentId)
          setState({ ...state, comments: new_comments })
        }
      })
  }
  // React.useEffect(() => {
  //   alert('updating ' + props.UserId)
  //   if (props.UserId !== 0){
  //     setState({...state, comments: state.comments})
  //   }
  // }, [UserId])
  return (<>
    <Menu
      id="simple-menu"
      anchorEl={anchorEl}
      keepMounted
      open={Boolean(anchorEl)}
      onClose={handleClose}
    >
      <MenuItem onClick={() => { handleClose(); handleDelete(); }} ></MenuItem>
    </Menu>
    <Card className={classes.root}>

      <CardHeader
        className={classes.header}
        avatar={
          <Avatar variant="rounded" aria-label="" className={classes.avatar} >
            {props.Username && props.Username.charAt(0)}
          </Avatar>
        }
        action={UserId === LoggedInUserId &&
          <IconButton onClick={handleClick} aria-label="settings">
            <MoreVertIcon />
          </IconButton>
        }
        title={props.Username}

        subheader={<Link
          to={`/survey/${props.ElementId}`} 
          style={{ textDecoration: 'none' }}>
            {props.Date}
        </Link>}
        classes={{ action: classes.action }}
      />
      <CardContent
        className={classes.content}
      >
        {/* {Children.map(props.children, child => cloneElement(child, {votedCallback}, {flipped}))} */}
        {/* {Children.map(props.children, child => cloneElement(child, {flipped: flipped, votedCallback: votedCallback}))} */}
        {/* {props.children.length} */}
        {Children.map(props.children, child => cloneElement(child, { flipped: flipped, votedCallback: votedCallback }))}
        {/* { props.children} */}
      </CardContent>
      <CardActions
        className={classes.actions}
        disableSpacing
      >
        <Tooltip title={answered ? ' ' : '  '}>
          <IconButton className={classes.icon} aria-label="add to favorites">
            <CheckIcon color={answered ? 'secondary' : 'primary'} />
          </IconButton>
        </Tooltip>
        {props.AnsweredAmount > 0 && props.AnsweredAmount}
        <IconButton className={classes.icon} aria-label="add to favorites" onClick={handleLike}>
          <FavoriteIcon color={voted ? 'secondary' : 'primary'} />
        </IconButton>
        {likes > 0 && likes}
        {answered &&
          <IconButton className={classes.icon} aria-label="add to favorites" onClick={handleFlip}>
            <LoopIcon />
          </IconButton>
        }
        {props.Tags.map(tag =>
          <div className={classes.tag}>{tag}</div>
        )}
        <Typography style={{ marginLeft: "auto", marginRight: 5, marginBottom: 0 }} paragraph> ({state.comments.length})</Typography>
        {!props.SeparateSurvey && <IconButton
          className={clsx(classes.expand, {
            [classes.expandOpen]: expanded,
          })}
          onClick={handleExpandClick}
          aria-expanded={expanded}
          aria-label="show more"
        >
          <ExpandMoreIcon />
        </IconButton>}
      </CardActions>
      <Collapse style={{ borderTop: "1px solid #e0e0e0" }} in={props.SeparateSurvey ? true && props.CommentsSection !== false: expanded} timeout="auto" unmountOnExit>
        <CardContent>
          <List>
            {state.comments.map(comment => <>
              <ListItem style={{ padding: 0, paddingLeft: 10 }} alignItems="flex-start">
                <ListItemAvatar>
                  <Avatar style={{ width: 30, height: 30 }} variant="rounded" aria-label="" className={classes.avatar} >
                    {comment.username && comment.username.charAt(0)}
                  </Avatar>
                </ListItemAvatar>

                <ListItemText
                  primary={<>
                    {comment.username + "  "}
                    <Typography variant="caption" component="span" color="textSecondary">
                      {comment.date}
                    </Typography>
                  </>}
                  secondary={
                    <React.Fragment>
                      <div style={{ display: "flex", wordBreak: "break-word" }}>

                        <Typography
                          component="span"
                          variant="body2"
                          className={classes.inline}
                          color="textPrimary"
                        >
                          {comment.content}
                        </Typography>
                        <div style={{ marginLeft: "auto" }}>
                          {comment.user_id === props.LoggedInUserId && <IconButton className={classes.icon} onClick={e => handleDeleteComment(comment.id)}>
                            <DeleteOutlineOutlinedIcon />
                          </IconButton>}
                          <Typography component="span">
                            {comment.likes != 0 && comment.likes}
                          </Typography>

                          <IconButton color={comment.voted ? "secondary" : "primary"} className={classes.icon} aria-label="add to favorites" onClick={e => { handleCommentLike(comment) }}>
                            <ThumbUpAltIcon />
                          </IconButton>
                        </div>
                      </div>

                    </React.Fragment>
                  }
                />
              </ListItem>
              <Divider variant="inset" component="li" />
            </>)}
          </List>
          <FormControl style={{ display: "flex" }}>
            <form onSubmit={handleCommentSubmit}>
              <TextField
                style={{ display: "flex" }}
                id="standard-textarea"
                label=""
                placeholder=""
                multiline
                onBlur={(e) => { setComment(e.target.value); e.target.value = "" }}
              />
              <FormHelperText error={errors.comment !== null && errors.comment !== ""}>{errors.comment}</FormHelperText>
              <Button onClick={handleCommentSubmit} type="submit" variant="contained" color="primary" >
                
            </Button>
            </form>
          </FormControl>
        </CardContent>
      </Collapse>

    </Card>
  </>);
}
