import React from 'react';
// import { makeStyles } from '@material-ui/core/styles';
import Card from '@material-ui/core/Card';
import CardContent from '@material-ui/core/CardContent';

import SportsBaseballIcon from '@material-ui/icons/SportsBaseball';
import MusicNoteIcon from '@material-ui/icons/MusicNote';
import MovieIcon from '@material-ui/icons/Movie';
import FavoriteIcon from '@material-ui/icons/Favorite';

import List from '@material-ui/core/List';
import ListItem from '@material-ui/core/ListItem';
import ListItemIcon from '@material-ui/core/ListItemIcon';
import ListItemText from '@material-ui/core/ListItemText';
import Divider from '@material-ui/core/Divider';
import { makeStyles } from '@material-ui/core/styles';
import Checkbox from '@material-ui/core/Checkbox';
import Container from '@material-ui/core/Container';
import Button from '@material-ui/core/Button';
import { useLocation } from 'react-router-dom'
import { properties } from '../Properties/Properties.js';
import HelpOutlineIcon from '@material-ui/icons/HelpOutline';

import Dialog from '@material-ui/core/Dialog';
import DialogActions from '@material-ui/core/DialogActions';
import DialogContent from '@material-ui/core/DialogContent';
import DialogContentText from '@material-ui/core/DialogContentText';
import DialogTitle from '@material-ui/core/DialogTitle';


import Popper from '@material-ui/core/Popper';
import Typography from '@material-ui/core/Typography';
import Fade from '@material-ui/core/Fade';
import Paper from '@material-ui/core/Paper';

const useStyles = makeStyles((theme) => ({
  list: {
    padding: 0
  },
  checkbox: {
    padding: 3
  },
  tag: {
    margin: 2,
    backgroundColor: "#a2a2dc",
    margin: 2,
    borderRadius: 5,
    cursor: "pointer",
    '&:hover': {
      backgroundColor: "#c3c3ec",
    },
    fontSize: 10,
    color: "#2f4f4f",
    // paddingLeft: 5,
    // paddingRight: 5,
    padding: 2,
    width: "max-content"
  },
}))
export default function CategoriesComponent(props) {
  const classes = useStyles()
  const [categories, setCategories] = React.useState([])
  const [tags, setTags] = React.useState([])
  const [checked, setChecked] = React.useState([0]);


  const [anchorEl, setAnchorEl] = React.useState(null);
  const [open, setOpen] = React.useState(false);

  let location = useLocation();

  const handleClick = event => {
    setAnchorEl(event.currentTarget);
    // setOpen((prev) => placement !== newPlacement || !prev);
    setOpen(true)
  };
  const handleClose = e => {
    setOpen(false)
  }

  React.useEffect(() => {
    fetch(`${properties.address}/getCategories`,
      {
        method: 'GET',
        redirect: 'follow',
        credentials: 'include',
      })
      .then(response => {
        if (response.status === 200) {
          return response.json()
        }
      })
      .then(result => {
        setChecked(result.filter(category => category.checked === true)
          .map(category => category.name))
        setCategories(result)
      })

    fetch(`${properties.address}/getAllTags`,
      {
        method: 'GET',
        redirect: 'follow',
        credentials: 'include',
      })
      .then(response => {
        if (response.status === 200) {
          return response.json()
        }
      })
      .then(result => {
        setTags(result)
      })
  }, [])
  const sendChecked = newChecked => {
    let body = {
      "skipped": categories
        .filter(category => !newChecked.includes(category.name))
        .map(category => category.name)
    }

    fetch(`${properties.address}/updateChecked`,
      {
        method: 'POST',
        headers: {
          'Content-Type': 'application/json'
        },
        body: JSON.stringify(body),
        redirect: 'follow',
        credentials: 'include',
      })
      .then(response => {
        if (response.status === 200) {
          props.Load(undefined, "")
        }
      })
  }
  const handleToggle = (value) => () => {
    const currentIndex = checked.indexOf(value);
    const newChecked = [...checked];

    if (currentIndex === -1) {
      newChecked.push(value);
    } else {
      newChecked.splice(currentIndex, 1);
    }

    setChecked(newChecked);
    if (props.LoggedIn) {
      sendChecked(newChecked)
    }
    else {
      props.Load(value, "")
    }
  }
  const handleTag = name => {
    props.Load(name, "")
  }

  return (<>
    <Card>
      <CardContent>
        <List component="nav" aria-label="main mailbox folders">
          <ListItem className={classes.list}>
            <ListItemText primary="" />
            {!props.LoggedIn && <ListItemIcon edge="end" style={{ minWidth: "auto" }}
              onMouseOver={handleClick}
              onMouseOut={handleClose}
            >
              <HelpOutlineIcon />
            </ListItemIcon>
            }
          </ListItem>
          <Divider />
          {categories.map(category => {
            const labelId = `checkbox-list-label-${category.name}`
            return (<ListItem className={classes.list} key={`category-${category.id}`} button onClick={handleToggle(category.name)}>
              {props.LoggedIn && <ListItemIcon>
                <Checkbox
                  className={classes.checkbox}
                  edge="start"
                  checked={checked.indexOf(category.name) !== -1}
                  tabIndex={-1}
                  disableRipple
                  inputProps={{ 'aria-labelledby': labelId }}
                />
              </ListItemIcon>}
              {/* <ListItemIcon>
                    {category.icon}
               </ListItemIcon> */}
              <ListItemText primary={category.name} />
            </ListItem>)
          }
          )}
        </List>
      </CardContent>
    </Card>
    <br></br>
    <Card style={{ height: 150, overflowY: "auto" }}>
      <CardContent >
        <List component="nav" aria-label="main mailbox folders">
          <ListItem className={classes.list}>
            <ListItemText primary="" />
          </ListItem>
          <Divider />
        </List>
        <div style={{ alignContent: "center" }}>
          {tags.map(tag =>
            <Button
              onClick={e => handleTag(tag.name)}
              className={classes.tag}
              color="primary"
              variant="contained"
              key={tag.id}
            >
              {tag.name}
            </Button>
            // <Button  color = "primary" variant = "contained" key = {tag.id}>{tag.name}</Button>
          )}
        </div>

      </CardContent>

    </Card>
    <Popper open={open} anchorEl={anchorEl} placement="top" transition>
      <Paper style={{ padding: 10 }}>
        <Typography variant="caption" className={classes.typography} style={{ width: 200 }}>
              
          </Typography>
      </Paper>
    </Popper>
  </>)
}