import React from 'react';
import { useParams, useHistory } from 'react-router-dom'

import MultipleSurveyComponent from "./MultipleSurveyComponent";
import SingleSurveyComponent from "./SingleSurveyComponent";
import RatingSurveyComponent from "./RatingSurveyComponent";
import CardComponent from "./CardComponent"
import CircularProgress from '@material-ui/core/CircularProgress'
import { properties } from '../Properties/Properties.js';

export default function SeparateSurveyComponent(props) {
    let { id } = useParams()
    id = id === undefined ?  props.SurveyId : id
    const [survey, setSurvey] = React.useState()
    const history = useHistory()
    React.useEffect(() => {
        fetch(`${properties.address}/survey?id=${id}`,
            {
                method: 'GET',
                credentials: 'include',
            })
            .then(response => {
                if (response.status === 200) {
                    return response.json()
                }
            })
            .then(result => {
                if (result) {
                    setSurvey(result)
                }
            })
    }, [])
    const renderBody = e => {
        let body = null
        switch (survey.type) {
            case "Single":
                body = <SingleSurveyComponent
                    Image={survey.image}
                    Voted={survey.answered}
                    History={history}
                    Question={survey.question}
                    Options={survey.options}
                    SurveyId={survey.id}
                />
                break
            case "Multiple":
                body = <MultipleSurveyComponent
                    Image={survey.image}
                    Voted={survey.answered}
                    History={history}
                    Question={survey.question}
                    Options={survey.options}
                    SurveyId={survey.id}
                />
                break
            case "Rating":
                body = <RatingSurveyComponent
                    Image={survey.image}
                    Voted={survey.answered}
                    History={history}
                    Question={survey.question}
                    Rows={survey.rows}
                    Columns={survey.columns}
                    SurveyId={survey.id}
                />
                break
            default:
                break
        }
        return body
    }
    return <>
        {!survey && <CircularProgress />}
        {survey &&
            <CardComponent
                AnsweredAmount={survey.answered_amount}
                Voted={survey.voted}
                ElementId={survey.id}
                key={survey.id}
                Username={survey.username}
                Likes={survey.likes}
                Answered={survey.answered}
                Tags={survey.tags}
                Comments={survey.comments}
                UserId={survey.user_id}
                LoggedInUserId={props.UserId}
                LoggedInUsername={props.UserName}
                SeparateSurvey={true}
                Date={survey.date}
                CommentsSection = {props.CommentsSection}
            >
                {renderBody()}
            </CardComponent>}
    </>
}