import React, { Fragment } from "react";
import request from "superagent";
import debounce from "lodash.debounce";
import { useRef } from 'react'
import MultipleSurveyComponent from "./MultipleSurveyComponent";
import SingleSurveyComponent from "./SingleSurveyComponent";
import RatingSurveyComponent from "./RatingSurveyComponent";
import CardComponent from "./CardComponent";
import { useHistory } from "react-router-dom";
import TouchRipple from "@material-ui/core/ButtonBase/TouchRipple";
import { properties } from '../Properties/Properties.js';

export default function InfiniteScroll(props) {
  const history = useHistory();
  const ref = useRef(null)
  const [state, setState] = React.useState({
    error: props.Error,
    hasMore: props.HasMore,
    isLoading: props.IsLoading,
    surveys: props.Surveys,
    index: props.Index,
    activeType: props.ActiveType
  })

  const load = (tag, type) => {
    let url = props.Mode === 'Top' ? `${properties.address}/surveys-top?index=` : `${properties.address}/surveys?index=`

    url += state.index
    tag !== undefined ? url += "&tag=" + tag : url += "&tag="
    type !== undefined ? url += "&type=" + type : url += "&type="

    if (props.Mode)
      request
        .get(url)
        .withCredentials()
        .then((results) => {
          const new_list = results.body.map(el => {
            let body
            switch (el.type) {
              case "Single":
                body = <SingleSurveyComponent Image={el.image} Voted={el.answered} History={history} Question={el.question} Options={el.options} SurveyId={el.id} />

                break
              case "Multiple":
                body = <MultipleSurveyComponent Image={el.image} Voted={el.answered} History={history} Question={el.question} Options={el.options} SurveyId={el.id} />
                break
              case "Rating":
                body = <RatingSurveyComponent Image={el.image} Voted={el.answered} History={history} Question={el.question} Rows={el.rows} Columns={el.columns} SurveyId={el.id} />
                break
              default:
                body = <div>,    -  </div>
            }
            let survey = <CardComponent
              AnsweredAmount={el.answered_amount}
              Voted={el.voted}
              ElementId={el.id}
              key={el.id}
              Username={el.username}
              Likes={el.likes}
              Answered={el.answered}
              Tags={el.tags}
              Comments={el.comments}
              UserId={el.user_id}
              Date={el.date}
              // Date={"03.03.3030"}
              LoggedInUserId={props.UserId}
              LoggedInUsername={props.UserName}
            >
              {body}
            </CardComponent>
            return survey
          })
          setState({
            ...state,
            hasMore: (state.surveys.length < 100) && results.body.length !== 0,
            isLoading: false,
            surveys: [
              ...state.surveys,
              ...new_list
            ],
            index: state.index + 1
          })
        })
  }

  React.useEffect(() => {
    load(undefined, props.ActiveType)
    if (props.SetReloadFunction) {
      props.SetReloadFunction(load)
    }
  }, [])

  window.onscroll = debounce(() => {
    if (error || isLoading || !hasMore) return;
    // Checks that the page has scrolled to the bottom
    if (ref.current &&
      window.innerHeight + document.documentElement.scrollTop
      > ref.current.offsetTop + ref.current.clientHeight
    ) {
      setState({ ...state, isLoading: true })
      load(undefined, props.ActiveType)
    }
  }, 300)

  const {
    error,
    hasMore,
    isLoading,
  } = state;

  return <>
    <div ref={ref}>
      {/* {props.Mode} */}
      {state.surveys.map((survey, index) => (
        <Fragment key={survey.props.ElementId}>
          {/* {survey.props.ElementId} */}
          {survey}
        </Fragment>
      ))}
      <hr />
      {error &&
        <div style={{ color: '#900' }}>
          {error}
        </div>
      }
      {isLoading &&
        <div>Loading...</div>
      }
      {/* {!hasMore &&
        <div>You did it! You reached the end!</div>
      } */}
    </div>
  </>
}