import React from 'react';
import MenuItem from '@material-ui/core/MenuItem';
import IconButton from '@material-ui/core/IconButton';
import Menu from '@material-ui/core/Menu';
import Divider from '@material-ui/core/Divider';
import AccessibilityNewIcon from '@material-ui/icons/AccessibilityNew';
import { Link } from 'react-router-dom'
import { properties } from '../Properties/Properties.js';

export default function AccountIconComponent(props) {
  const [anchorEl, setAnchorEl] = React.useState(null);

  const handleClick = (event) => {
    setAnchorEl(event.currentTarget);
  };
  const signOut = e => {
    fetch(`${properties.address}/signout`,
      {
        method: 'POST',
        credentials: 'include',
      })
      .then(response => response.text())
      .then(result => {
        if (result === 'signedout') {
          props.setState({
            loggedIn: false,
            username: ''
          })
        }
      })
  }
  const handleClose = () => {
    setAnchorEl(null);
  };
  return (<>
    <IconButton onClick={handleClick}>
      <AccessibilityNewIcon color='action' />
    </IconButton>
    <Menu
      id="simple-menu"
      anchorEl={anchorEl}
      keepMounted
      open={Boolean(anchorEl)}
      onClose={handleClose}
    >
      <Link to='/profile' style={{ textDecoration: "none" }}>
        <MenuItem onClick={handleClose} > </MenuItem>
      </Link>
      <Divider />
      <Link to='/createSurvey' style={{ textDecoration: "none" }}>
        <MenuItem onClick={handleClose}> </MenuItem>
      </Link>

      <Link to='/home' style={{ textDecoration: "none" }}>
        <MenuItem onClick={() => { handleClose(); signOut(); }}></MenuItem>
      </Link>
    </Menu>
  </>)
}