import React from 'react';

import FormControlLabel from '@material-ui/core/FormControlLabel';
import FormControl from '@material-ui/core/FormControl';
import FormLabel from '@material-ui/core/FormLabel';
import FormHelperText from '@material-ui/core/FormHelperText';
import Button from '@material-ui/core/Button';
import Checkbox from '@material-ui/core/Checkbox';
import FormGroup from '@material-ui/core/FormGroup';
import StatisticsComponent from './StatisticsComponent'
import Grid from '@material-ui/core/Grid';
import { properties } from '../Properties/Properties.js';

export default function SingleSurveyComponent (props){
    const [state, setState] = React.useState({
        choosedOption: "",
        value: "",
        helperText: " ,    ",
        error: false,
        img: props.Image,
    })
    const [options, setOptions] = React.useState({})
    const [voted, setVoted] = React.useState(props.Voted)
    const [SurveyId, setSurveyId] = React.useState(props.SurveyId)
    const [img, setImg] = React.useState(props.Image)
    const handleSubmit = (event) => {
      event.preventDefault();

      if (Object.values(options).filter(value => value === true).length === 0) {
        setState({...state, helperText: ' ', error: true})
      }
      else{
        setState({...state, helperText: ' ,    ', error: false})
      }
      fetch(`${properties.address}/answerSurvey`, 
      {
        method: 'POST',
        headers: {
          "Content-Type": "application/json"
        },
        body: JSON.stringify({
          "surveyId": props.SurveyId,
          "choosedOptions" : Object.keys(options).filter(key => options[key] === true)
          
        }),
        redirect: 'follow',
        credentials: 'include'
      })
      .then(response => {
          if (response.status === 200){
            props.votedCallback()
            setVoted(true)
          }
          else if (response.status === 404 || response.status === 403 ){
            props.History.push('/login')
          }
        }
      )
      
    }
    const handleChange = (event) => {
        setOptions({ ...options, [event.target.name]: event.target.checked });
    }
    const returnBody = e => {
      let body
      if (voted && !props.flipped){
        body = <StatisticsComponent Options = {props.Options} Type = "Multiple" SurveyId = {SurveyId}></StatisticsComponent>
      }
      else{
        body = <Grid container>
            <Grid item xs = {img === '' || img === null ? 0 : 4}>
              <img alt = "" style = {{width: "100%", maxWidth: "200px"}} src = {img}></img>
            </Grid>
            <Grid item xs = {img === "" || img === null ? 12 : 8}>
              <form onSubmit={handleSubmit}>
                <FormControl 
                  component="fieldset"
                  error = {state.error}
                >
                  <FormLabel component="legend">{props.Question}</FormLabel>
                  <FormGroup>
                      {props.Options.map( (option, index) =>
                          <FormControlLabel
                          control={<Checkbox name = {option} onChange = {handleChange} />}
                          label={option}
                          />
                      )}
                  </FormGroup>
                  <FormHelperText>{state.helperText}</FormHelperText>
                  <Button type = "submit" variant="contained" color="primary" onChange = {handleSubmit}></Button>
                </FormControl>
              </form>
            </Grid>
        </Grid>
        
      }
      return body
    }
      return <>
        {returnBody()}
      </>
    }